<template>
    <div class="shopWrap">
        <header>
            <div class="top">
                <div class="top_img">
                    <img :src="imgurl+getShopMsg.image_path" alt=""></div>
                <div class="top_text">
                    <h4>{{getShopMsg.name}}</h4>
                    <p>商家配送／分钟送达／配送费¥5</p>
                    <p>公告：欢迎光临，用餐高峰请提前下单，谢谢</p>
                </div>
            </div>
            <div class="lianjie">
                <router-link to="/food"><i class="el-icon-arrow-left"></i></router-link>
                <router-link to="/shop/shopdetail"><i class="el-icon-arrow-right"></i></router-link>
            </div>
        </header>
        <!--商品、评价-->
        <div class="change_show_type">
            <div @click="getshowgood">
                <span v-if="isShop">商品</span>
                <span class="changecolor" v-if="!isShop">商品</span>
            </div>
            <div @click="getshowpj">
                <span v-if="isPingjia">评价</span>
                <span class="changecolor" v-if="!isPingjia">评价</span>
            </div>
        </div>
        <!--左右选框-->
        <div class="food_container" v-if="showGoods">
            <div class="menu_container">
                <!--shop的左边导航栏shoplists-->
                <div class="wrapper_menu">
                    <ul>
                        <li v-for="(lis,index) in shoplists" :key="lis.id" @click="getShoplist(index)" :class="number == index ? 'activity_menu' : '' ">
                            <span>{{lis.name}}</span>
                            <!--<span class="shop_number">{{danpinsum}}</span>-->
                        </li>
                    </ul>
                </div>
                <div class="menu_right">
                    <!--shop右边的导航栏foodlist-->
                    <div class="menu_detail_list" v-for="lis in shoplists">
                        <!--shop右边的导航栏foodlist标题-->
                        <div class="menu_right_title">
                            <div>
                                <h2>{{lis.name}}</h2>
                                <span>{{lis.description}}</span>
                            </div>
                            <span>···</span>
                        </div>
                        <!--shop右边的导航栏foodlist商品-->
                        <section v-for="li in lis.foods">
                            <div class="menu_detail_link">
                                <div class="detail_left">
                                    <img :src="imgurl+li.image_path" alt="">
                                </div>
                                <div class="detail_right">
                                    <h3>
                                        <strong>{{li.name}}</strong>
                                        <span>招牌</span>
                                    </h3>
                                    <p class="text1">{{li.description}}</p>
                                    <p class="text2">
                                        <span>月售{{li.month_sales}}份</span>
                                        <span>好评率{{li.satisfy_rate}}%</span>
                                    </p>
                                    <p class="text3">
                                        <span>打威群岛</span>
                                    </p>
                                </div>
                            </div>
                            <div class="footer">
                                <div class="footer_left">
                                    <span class="p_one">￥</span>
                                    <span class="p_two">{{li.specfoods[0].price}}</span>
                                    <span class="p_three">起</span>
                                </div>
                                <div class="cart_module">
                                    <!--选规格点击事件和减号事件-->
                                    <div class="add_car_num" v-if="li.specifications.length ? showAddNum: !showAddNum ">
                                        <span  @click="xggjianshao" class="jianhao" v-if="foodNum(li)"><i class="el-icon-remove"></i></span>
                                        <span class="count" v-if="foodNum(li)">{{foodNum(li)}}</span>
                                        <span @click="xuanguige(li.specfoods,li.category_id)" class="jiahao" id="xgg" >选规格</span>
                                    </div>

                                    <!--加减号-->
                                    <div class="add_car_num" v-if="li.specifications.length ? !showAddNum : showAddNum">
                                        <span @click="jianshao(foodNum(li),li.category_id, li.item_id, li.specfoods[0].food_id, li.specfoods[0].name, li.specfoods[0].price, li.specfoods[0].specs, li.specfoods[0].packing_fee, li.specfoods[0].sku_id, li.specfoods[0].stock)" v-if="foodNum(li)" class="jianhao"><i class="el-icon-remove"></i></span>
                                        <span class="count" v-if="foodNum(li)">{{foodNum(li)}}</span>
                                        <span @click="tianjia(li.category_id, li.item_id, li.specfoods[0].food_id, li.specfoods[0].name, li.specfoods[0].price, li.specfoods[0].specs, li.specfoods[0].packing_fee, li.specfoods[0].sku_id, li.specfoods[0].stock)" class="jiahao"><i class="el-icon-circle-plus"></i></span>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </div>
                </div>
            </div>
            <!--底部购物车-->
            <div class="buy_cart_container">
                <div>
                    <div class="cart_icon_container" id="shop_car" @click="showCar" :style="allFoodsPrice ? 'background-color:#3190e8' : ''">
                        <i class="el-icon-shopping-cart-2"></i>
                        <span id="cart_list_length" v-if="allFoodsPrice">{{allItemCount}}</span>
                    </div>
                    <div class="cart_num">
                        <div class="pricecount">￥{{allFoodsPrice}}</div>
                        <div>配送费¥5</div>
                    </div>
                    <div class="gotopay" id="go_pay" @click="goDist" :style="allFoodsPrice ? 'background-color :#4cd964': ''">
                        <span id="paysum"@click="goDist">{{allFoodsPrice ? '去下单' : '还差¥20起送'}}</span>
                    </div>
                </div>
            </div>
        </div>
        <!--评价-->
        <div class="rating_container" v-if="!isPingjia">
            <div>
                <div class="rating_header">
                    <div class="rating_header_left">
                        <p class="t1">5</p>
                        <p class="t2">综合评价</p>
                        <p class="t3">高于周边商家76.9%</p>
                    </div>
                    <div class="rating_header_right">
                        <p class="p1">
                            <span>服务态度</span>
                            <span class="stars">
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                            </span>
                            <span class="yellow_t">4.7</span>
                        </p>
                        <p class="p2">
                            <span>菜品评价</span>
                            <span class="stars">
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                            </span>
                            <span class="yellow_t">4.7</span>
                        </p>
                        <p class="p3">
                            <span>送达时间</span>
                            <span class="stars">
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                                <i class="el-icon-star-on"></i>
                            </span>
                            <span class="yellow_t">4.7</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
        <div class="pingjia"  v-if="!isPingjia">
            <ul>
                <li v-for=" pj in pingjialists">{{pj.name}}({{pj.count}})</li>
            </ul>
        </div>
        <div class="jpcontent" v-for="pjtext in pingjiatext"  v-if="!isPingjia">
            <ul>
                <li>
                    <img :src="'https://elm.cangdu.org/img/'+'default.jpg'" alt="">
                    <div class="contents">
                        <div class="con_top">
                            <p>
                                <span>{{pjtext.username}}</span>
                                <span>{{pjtext.rated_at}}</span>
                            </p>
                            <p>
                                <span>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                    <i class="el-icon-star-on"></i>
                                </span>
                                <span>{{pjtext.time_spent_desc}}</span>
                            </p>
                        </div>
                        <div class="nav_lis">
                            <ul>
                                <li >
                                    <img :src="'https://fuss10.elemecdn.com/'+pic.image_hash" alt="" v-for="pic in pjtext.item_ratings" style="float: left">
                                </li>
                            </ul>
                        </div>
                        <div class="nav_block">
                            <ul>
                                <li v-for="pjblock in pjtext.item_ratings" class="ellipsis">{{pjblock.food_name}}</li>
                            </ul>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <!--选规格弹框-->
        <div class="specs_cover" v-if="showxgg"></div>
        <div class="specs_list" v-if="showxgg">
            <div class="specs_list_header">
                <h4>{{xuanguigeName}}</h4>
                <i class="el-icon-close"  @click="closethis"></i>
            </div>
            <div class="specs_details">
                <h5>规格</h5>
                <ul>
                    <li v-for="(xglist,index) in xuanguigeArr"
                        :key="index"
                        :style="guigebgc == index ? 'color:blue;' : ''"
                        @click="guigeID(index)">{{xglist.specs[0].value}}</li>
                    <!--<li>111</li>-->
                </ul>
            </div>
            <div class="specs_footer">
                <div class="specs_left">
                    <span>￥</span>
                    <span>{{guigeArr.price}}</span>
                </div>
                <div class="specs_right" @click="putCar(xuanguigecId, xuanguigeArr[guigebgc].item_id, xuanguigeArr[guigebgc].food_id, xuanguigeName, guigeArr.price, xuanguigeArr[guigebgc].specs[0].value, xuanguigeArr[guigebgc].packing_fee, xuanguigeArr[guigebgc].sku_id, xuanguigeArr[guigebgc].stock)">加入购物车</div>
            </div>
        </div>
        <!--购物车列表-->
        <el-collapse-transition>
            <div class="cart_food_list" v-if="showBuyCart">
            <div class="cart_food_list_header">
                <h4>购物车</h4>
                <div>
                    <i class="el-icon-delete"></i>
                    <span @click="clearAll">清空</span>
                </div>
            </div>
            <div class="cart_food_details">
                <ul>
                    <li v-for="(good1,index) in carArr" :key="index">
                        <div class="cart_list_num">
                            <p class="ellipsis">{{good1.name}}</p>
                            <p class="ellipsis" style="font-size:.5rem">{{good1.specs == '' ? '' : good1.specs}}</p>
                        </div>
                        <div class="cart_list_price">
                            <span>￥</span>
                            <span>{{good1.price}}</span>
                        </div>
                        <div class="add_car_num" style="display:block">
                            <span @click="jianshao1(good1.shopId,good1.category_id, good1.item_id, good1.food_id, good1.name, good1.price, good1.specs, good1.packing_fee, good1.sku_id, good1.stock)"><i class="el-icon-remove"></i></span>
                            <span>{{good1.num}}</span>
                            <span @click="tianjia1(good1.shopId,good1.category_id, good1.item_id, good1.food_id, good1.name, good1.price, good1.specs, good1.packing_fee, good1.sku_id, good1.stock)"><i class="el-icon-circle-plus"></i></span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        </el-collapse-transition>
    </div>
</template>

<script>
    import { mapState } from "vuex";

    export default {
        name: "Shop",
        data(){
            return {
                showGoods:true,
                showPingjia:false,
                imgurl:"https://elm.cangdu.org/img/",
                getShopMsg:[],
                getShopId:[],
                shoplists:[],
                shopArr:[],
                shopName:"",
                pingjialists:[],
                pingjiascore:[],
                pingjiatext:[],
                shopDescription:"",
                number:0,
                showAddNum:true,
                shopcount:0,
                shopSum:0,
                danpinsum:0,
                isShop:false,
                isPingjia:true,
                showxgg:false,
                c_sum:0,
                c_num:0,
                cart_list:[],
                showBuyCart:false,
                //修改后
                newArr:[],
                leftArr:[],
                leftNum:0,
                countNum:0,
                xggprice:0,
                xg_num:undefined,
                leftlength:[],

                //修改购物车
                carArr: [],
                shopCarts: null,
                xuanguigeArr: [],
                guigeArr: [],
                guigebgc: 90,
                xuanguigeName: "",
                xuanguigecId: "",
                shopId:""
            }
        },
        created(){
            this.number = localStorage.getItem("shopindex")-0;
            this.shopName = localStorage.getItem("shopname");
            this.shopDescription = localStorage.getItem("shopdes");
            this.getShopMsg = JSON.parse(localStorage.getItem("shopId"));
            // console.log(this.getShopMsg);
            this.shopId = this.getShopMsg.id;

            //获取左边的导航栏数据
            this.axios.get("/shopping/v2/menu", {
                params: {restaurant_id:this.getShopMsg.id}
            }).then(res=>{
                this.shoplists = res;
                console.log("123123123",this.shoplists)
            }).catch(err=>{});
            //获取评价的数据
            this.axios.get("/ugc/v2/restaurants/"+this.getShopMsg.id+"/ratings/tags").then(res=>{
                this.pingjialists = res;
            }).catch(err=>{});
            //获取评分的数据
            this.axios.get("/ugc/v2/restaurants/"+this.getShopMsg.id+"/ratings/scores").then(res=>{
                this.pingjiascore = res;
            }).catch(err=>{});
            //获取用户评价内容的数据
            this.axios.get("/ugc/v2/restaurants/"+this.getShopMsg.id+"/ratings").then(res=>{
                this.pingjiatext = res;
                console.log(res)
            }).catch(err=>{});
            // 初始化vuex
            this.$store.commit("init_buy_cart");

            // 处理购物车数据
            this.handleCartData();
        },
        watch: {
            shopCart: {
                handler(newV, oldV) {
                    console.log("shopCarts改变");
                    this.handleCartData();
                },
                deep: true
            }
        },
        methods:{
            handleCartData() {
                this.carArr = [];
                this.shopCarts = this.$store.state.cartList;
                console.log(this.shopCarts)
                Object.keys(this.shopCarts).forEach(shopId => {
                    console.log(this.shopCarts[shopId])
                    Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                        Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                            Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(
                                food_id => {
                                    let foodItem = this.shopCarts[shopId][category_id][item_id][
                                        food_id
                                        ];
                                    let newFoodItem = {
                                        ...foodItem,
                                        category_id,
                                        item_id,
                                        food_id,
                                        shopId
                                    };
                                    if (newFoodItem.shopId == this.shopId) {
                                        this.carArr.push(newFoodItem);
                                    }
                                }
                            );
                        });
                    });
                });
            },
            //普通添加
            tianjia(
                category_id,
                item_id,
                food_id,
                name,
                price,
                specs,
                packing_fee,
                sku_id,
                stock
            ) {
                this.$store.commit("add_cart", {
                    shopid: this.shopId,
                    category_id,
                    item_id,
                    food_id,
                    name,
                    price,
                    specs,
                    packing_fee,
                    sku_id,
                    stock
                });
            },
            //普通减少
            jianshao(
                i,
                category_id,
                item_id,
                food_id,
                name,
                price,
                specs,
                packing_fee,
                sku_id,
                stock
            ) {
                //console.log("减少", i, category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock);
                this.$store.commit("reduce_cart", {
                    shopid: this.shopId,
                    category_id,
                    item_id,
                    food_id,
                    name,
                    price,
                    specs,
                    packing_fee,
                    sku_id,
                    stock
                });
            },
            //选规格添加
            putCar(
                category_id,
                item_id,
                food_id,
                name,
                price,
                specs,
                packing_fee,
                sku_id,
                stock
            ) {
                console.log(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock)
                this.$store.commit("add_cart", {
                    shopid: this.shopId,
                    category_id,
                    item_id,
                    food_id,
                    name,
                    price,
                    specs,
                    packing_fee,
                    sku_id,
                    stock
                });
                this.showxgg = !this.showxgg;
            },
            //购物车列表添加
            tianjia1(
                shopId,
                category_id,
                item_id,
                food_id,
                name,
                price,
                specs,
                packing_fee,
                sku_id,
                stock
            ) {
                this.$store.commit("add_cart", {
                    shopid: shopId,
                    category_id,
                    item_id,
                    food_id,
                    name,
                    price,
                    specs,
                    packing_fee,
                    sku_id,
                    stock
                });
            },
            //购物车列表减少
            jianshao1(
                shopId,
                category_id,
                item_id,
                food_id,
                name,
                price,
                specs,
                packing_fee,
                sku_id,
                stock
            ) {
                this.$store.commit("reduce_cart", {
                    shopid: shopId,
                    category_id,
                    item_id,
                    food_id,
                    name,
                    price,
                    specs,
                    packing_fee,
                    sku_id,
                    stock
                });
            },
            showCar() {
                this.showBuyCart = !this.showBuyCart;
            },
            clearAll() {
                this.$store.commit("delete_cart", { shopid: this.shopId });
                this.showBuyCart = !this.showBuyCart;
            },
            xuanguige(i, cId) {
                this.showxgg = !this.showxgg;
                // console.log(i)
                this.xuanguigeArr = i;
                // console.log(this.xuanguigeArr);
                this.xuanguigeName = i[0].name;
                this.xuanguigecId = cId;
                // console.log(this.xuanguigeName)
            },
            closethis() {
                this.showxgg = !this.showxgg;
            },
            guigeID(i) {
                // console.log(i)
                this.guigeArr = this.xuanguigeArr[i];
                // console.log(this.guigeArr)
                this.guigebgc = i;
            },
            xggjianshao() {
                alert("请去购物车列表操作");
            },
            goDist() {
                this.$router.push("/confirmorder");
                // console.log(this.allFoodsPrice)
                localStorage.setItem('allprice',this.allFoodsPrice)
            },
            getShoplist(sum){
                // console.log(this.$el)
                let selector = ".menu_detail_list:nth-of-type(" + (sum+1) + ")";
                console.log(this.$el.querySelector(selector));
                this.$el.querySelector(selector).scrollIntoView({
                    behavior: "smooth",  // 平滑过渡
                    block:    "start"  // 上边框与视窗顶部平齐。默认值
                });
                this.number = sum;
                localStorage.setItem("shopindex",sum);
                localStorage.setItem("foodlist",JSON.stringify(this.shoplists[sum].foods));
            },
            //显示商品栏
            getshowgood(){
                this.isShop = !this.isShop;
                if( this.isPingjia == false){
                    this.isPingjia = true;
                }
                this.showGoods = true
            },
            //显示评价栏
            getshowpj(){
                this.isPingjia = !this.isPingjia;
                if( this.isShop == false){
                    this.isShop = true;
                }
                this.showGoods = false
            },
        },
        computed: {
            //总价格
            allFoodsPrice() {
                let allFoodsCount = 0;
                if (this.shopCarts) {
                    // let allFoods = this.$store.state.cartList;
                    Object.keys(this.shopCarts).forEach(shopId => {
                        Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                            Object.keys(this.shopCarts[shopId][category_id]).forEach(
                                item_id => {
                                    Object.keys(
                                        this.shopCarts[shopId][category_id][item_id]
                                    ).forEach(food_id => {
                                        let foodItem = this.shopCarts[shopId][category_id][item_id][
                                            food_id
                                            ];
                                        let newFoodItem = {
                                            ...foodItem,
                                            category_id,
                                            item_id,
                                            food_id,
                                            shopId
                                        };
                                        if (newFoodItem.shopId == this.shopId) {
                                            allFoodsCount += newFoodItem.num * newFoodItem.price;
                                        }
                                    });
                                }
                            );
                        });
                    });
                }
                return allFoodsCount;
            },
            //购物车总数量
            allItemCount() {
                let allItemCount = 0;
                if (this.shopCarts) {
                    // let allFoods = this.$store.state.cartList;
                    // console.log(this.shopCarts)
                    Object.keys(this.shopCarts).forEach(shopId => {
                        Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                            Object.keys(this.shopCarts[shopId][category_id]).forEach(
                                item_id => {
                                    Object.keys(
                                        this.shopCarts[shopId][category_id][item_id]
                                    ).forEach(food_id => {
                                        let foodItem = this.shopCarts[shopId][category_id][item_id][
                                            food_id
                                            ];
                                        let newFoodItem = {
                                            ...foodItem,
                                            category_id,
                                            item_id,
                                            food_id,
                                            shopId
                                        };
                                        if (newFoodItem.shopId == this.shopId) {
                                            allItemCount += newFoodItem.num;
                                        }
                                    });
                                }
                            );
                        });
                    });
                }
                return allItemCount;
            },
            //分类总数量
            iCount(){
                return func1 =>{

                }
            },
            itemCount() {
                return cateid => {
                    let itemFoodCount = 0;
                    if (this.shopCart && this.shopCart[cateid]) {
                        Object.values(this.shopCart[cateid]).forEach(foods => {
                            Object.values(foods).forEach(food => {
                                // console.log(food);
                                itemFoodCount += food.num;
                            });
                        });
                    }
                    return itemFoodCount;
                };
            },
            ...mapState(["cartList"]),
            //购物车
            shopCart: function() {
                return Object.assign({}, this.$store.state.cartList[this.shopId]);
            },
            //商品数量
            foodNum() {
                return foods => {
                    let category_id = foods.category_id;
                    let item_id = foods.item_id;
                    let itemNum;
                    if (
                        this.shopCart &&
                        this.shopCart[category_id] &&
                        this.shopCart[category_id][item_id]
                    ) {
                        let num = 0;
                        Object.values(this.shopCart[category_id][item_id]).forEach(
                            (item, index) => {
                                itemNum = item ? item.num : 0;
                                num += itemNum;
                            }
                        );
                        return itemNum;
                    } else {
                        return 0;
                    }
                };
            }
        }
    }
</script>

